.btn{
    border: none;
    margin: 5px auto;
    width: 150px;
    height: 50px;
    background-color: #CCFF99;
    outline: none;
    -webkit-transition:0.2s;
    -o-transition:0.2s;
    transition:0.2s;
}
.filter-active{
    background-color: rgb(138, 243, 32)!important;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
.btn-wrapper{
    margin: 0 auto;
    width: 400px;
}


